import React, { Component } from 'react';
import classNames from 'classnames';
import { inject, observer } from 'mobx-react';
import * as Tool from 'tool';

import styles from './service-info.scss';

// 透明服务介绍
@inject('Toast', 'ComNoContent', 'Header')
@observer
export default class ServiceInfo extends Component {

  state = {
    imgArr: [
      require('src/assets/images/guide/info-1.png'),
      require('src/assets/images/guide/info-2.png')
    ]
  }

  componentDidMount () {
    this.props.Header.$setTitle('透明服务');
    Tool.setTitle('透明服务');
  }

  render () {
    const { imgArr } = this.state;
    return (
      <div className={styles.info_item}>
        {
          imgArr.map((item, index) => (
            <img src={item} key={index}></img>
          ))
        }
      </div>
    );
  }
}